<template>
    <div class="main-content">
        <div class="breadcrumbs" id="breadcrumbs">
            <ul class="breadcrumb">
                <li>
                    <i class="icon-home home-icon">
                    </i>
                    <a href="#">
                    </a>
                </li>
                <li class="active">
                    <router-link to="/whd">
                        微互动
                    </router-link>
                </li>
            </ul>
        </div>
        <div class="page-content ha_page_content">
            <div class="el-row contBox clear">
                <div class="cs-top">
                    <el-col :span="6">
                            <el-button type="primary" @click="wfbClick($event)" :plain="is_xz">
                                已发布
                            </el-button>
                            <el-button type="primary"  @click="yfbClick($event)" :plain="!is_xz">
                                未发布
                            </el-button>
                    </el-col>
                    <el-col :span="4">
                        <el-button type="primary" :plain="true" @click="xinzengclick">
                            新增
                        </el-button>
                    </el-col>
                    <el-col :offset="10" :span="4">
                        <el-input :on-icon-click="handleIconClick" icon="search"  v-model="input2">
                        </el-input>
                    </el-col>
                </div>
                <div class="cs-list-table">
                    <!-- 列表 -->
                    <el-row :gutter="40">
                        <el-col :span="13">
                            <el-table :data="q" border="" height="250" style="width: 100%">
                                <el-table-column label="序号" prop="" width="70">
                                </el-table-column>
                                <el-table-column label="标题名称" prop="" width="210">
                                </el-table-column>
                                <el-table-column label="发布状态" prop="" width="100">
                                </el-table-column>
                                <el-table-column label="发布人" prop="" width="100">
                                </el-table-column>
                                <el-table-column label="更新时间" prop="" width="150">
                                </el-table-column>
                                <el-table-column label="更改人" prop="">
                                </el-table-column>
                            </el-table>
                        </el-col>
                        <el-col :span="11" style="border: 1px solid #dfe6ec;padding: 0;">
                            <wenbencomponent :is_show="is_show" :parmas="parmas"></wenbencomponent>
                        </el-col>
                    </el-row>
                </div>
            </div>
        </div>
        <xzfzpcomponent v-if="is_xzshow" ></xzfzpcomponent>
        
    </div>
</template>
<script>
    export default {
        data(){
            return {
                is_show:true,
                parmas:{
                    title:"标题",
                    message:"内容",
                    btn01:"编辑",
                    btn02:"删除",
                    btn03:"撤下",
                },
                is_xz:false,
                is_xzshow:false,

            }
        },
        components: {
            xzfzpcomponent : function (resolve, reject) {
                require(['./fzp_xinzeng.vue'],  resolve)
            },
            wenbencomponent : function (resolve, reject) {
                require(['./wenben.vue'],  resolve)
            }
        },
        methods:{
            wfbClick(){
                this.parmas={
                    title:"标题",
                    message:"内容",
                    btn01:"编辑",
                    btn02:"删除",
                    btn03:"撤下",
                }
                this.is_xz=false;
            },
            yfbClick(){
                this.parmas={
                    title:"标题",
                    message:"内容",
                    btn01:"编辑",
                    btn02:"删除",
                    btn03:"发布",
                }
                this.is_xz=true;
            },
            handleIconClick(){

            },
            xinzengclick(){
                this.is_xzshow=true;
            }
        },
        created(){
            var that=this;
            var Bus=global.eventHub
            Bus.$on("deteldata",function(){
                that.is_xzshow=false;
            });
        }

}
</script>
<style scoped="">
  .cs-top{
  overflow: hidden;
}
  .cs-top .el-col{
    padding: 20px 0;
    line-height: 36px;
  }
</style>


